.main107{
    display: flex;
    justify-content: space-between;
    width: 100%;

}
.preview107{
    margin: 15px;
    width: 100%;
}
.header107{
    display: flex;
    justify-content: space-between;
    height: fit-content;
    background-color: rgb(58, 84, 109);
    
}

.header107 p{
    color: white;
    font-size: xx-small;
}
.header107 h3{
    color: white;
    
}

@media (min-width:768px){
    .main107{
        display: flex;
        justify-content: space-between;
        width: 100%;
    
    }
    .preview107{
        margin: 15px;
        width: 100%;
    }
    .header107{
        display: flex;
        justify-content: space-between;
        height: fit-content;
        background-color: rgb(58, 84, 109);
        
    }
    
    .header107 p{
        color: white;
        font-size: xx-small;
    }
    .header107 h3{
        color: white;
        
    }   
    .resume-download-section0{
        width: 60%;
    }
}

@media (max-width:768px){
    .main107{
        display: flex;
        justify-content: space-between;
        width: 100%;
        flex-direction: column;
    
    }
    .preview107{
        margin: 0px;
        width: 100%;
    }
    .preview107 p,.preview107 li{
        font-size: xx-small;
    }
    .preview107 h6{
        font-size: small;
        font-weight: 700;
    }
    
    .header107 p{
        color: white;
        font-size:.3rem;
    }
    .header107 h3{
        color: white;
        font-size: small;
        
    }
    .imagediv107{
        margin-left: 0px;
        width: 25%;
        height: 100px;
        margin-top: 10px;
    }
    .image107{
    height: 100px;
    }
    
    .preview107 h3{
        margin-bottom: -2px;
    }
    .profilediv107 p{
    font-size: .3rem;    
    }
    
    
}


